			        lower: function(e) {
			          //  console.log(e)
						var len=this.datalist.length
						if(len>50){
						uni.showToast({
							icon:'none',
							title:'没有更多数据了',
							duration:2000
						})	
						return
						}else{
						uni.showLoading({
