								map.Name='文山'+i
								map.Quantity=i
								map.Amount=10+i
								this.datalist.push(map)
							} 
							
							uni.hideLoading();
							
						},2000)
						}
			        },
			        scroll: function(e) {
			           // console.log(e)
			            this.old.scrollTop = e.detail.scrollTop
			        },
			        goTop: function(e) {
			            this.scrollTop = this.old.scrollTop
			            this.$nextTick(function() {
			                this.scrollTop = 0
			            });
